.registerContainer{
  width: 300px;
  height: 250px;
  padding: 20px;
  position: absolute;
  border-radius: 10px;
  background-color: #fff;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.logo{
   display: flex;
   align-items: center;
   color: slateblue;
   display: flex;
   align-items: center;
   font-weight: 700;
   cursor: pointer;
   position: relative;
}

