}

.registerBtn{
  border: none;
  padding: 5px;
  border-radius: 5px;
  color: #fff;
  background-color: slateblue;
}

.success{
 color: #00b894;
 font-size: 12px;
 text-align: center;
}

.failure{
    color: #ff6348;
    font-size: 12px;
    text-align: center
}

.registerCancel{
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}